<script>
import Navbar from "../components/Header/Navbar";
import Footer from "../components/Header/Footer";

import {LMap, LTileLayer, LMarker, LIcon} from 'vue2-leaflet';
import icon from "@/assets/img/maps_icon.png";
import axios from "axios";
import router from "@/router";
export default {
  components: {
    Footer,
    Navbar,
    LMap,
    LTileLayer,
    LMarker,
    LIcon,
  },
  data() {
    return {
      img_icon: icon,
      url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
      zoom: 15,
      isselcts: false,
      isselcts1: false,
      year: '2022',
      isActive_m: false,
      selected: '',
      selected1: 'Dekabr',
      month: '',
      options: [
        {value: null, text: 'Please select some item'},
        {value: 'a', text: 'This is First option'},
        {value: 'b', text: 'Default Selected Option'},
        {value: 'c', text: 'This is another option'},
        {value: 'd', text: 'This one is disabled', disabled: true}
      ],
      options1: [
        {value: null, text: 'Please select some item'},
        {value: 'a', text: 'This is First option'},
        {value: 'b', text: 'Default Selected Option'},
        {value: 'c', text: 'This is another option'},
        {value: 'd', text: 'This one is disabled', disabled: true}
      ],
      center:{
        lat:'',
        lng:''
      }
    }
  },
  mounted() {
    axios.get('/api/heritage/'+router.currentRoute.params.id+'/',{
        headers:{
          'Accept-Language':localStorage.getItem('lang')
        }
    })
        .then(res=>{
          this.$store.state.heritage_id=res.data
          this.center={
            "lat":res.data.latitude,
            "lng":res.data.longitude
          }
        })
  },
  methods: {
    openSelects() {
      this.isselcts = !this.isselcts
      this.isselcts1 = false

    },
    openSelects1() {
      this.isselcts1 = !this.isselcts1
      this.isselcts = false

    },
    filter() {
      console.log(this.year)
      console.log(this.month)
      this.$store.dispatch('newsMfiltr', {
        "year": this.year,
        "month": this.month
      })
    }
  }
}
</script>
